$primary-color: #34d19f;
$secondary-color: #f5f5f5;
$tertiary-color: #111111;

$main-white: #ffff;

$primary-color-transparent: rgba(51, 209, 159, 0.85);
$secondary-color-transparent: rgba(245, 245, 245, 0.85);
$tertiary-color-transparent: rgba(18, 18, 18, 0.85);

$primary-color-monochromatic: #27ab81;
$secondary-color-monochromatic: #dcdcdc;

%card {
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
  background-color: white;
  position: absolute;

  &:hover {
    cursor: pointer;
  }
}
